.header {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  padding: 28px 24px 0;
  z-index: 10;
}

.header__logo {
  font-size: 28px;
  font-weight: 600;
  color: $black;
  text-transform: uppercase;
  transition: color .2s linear;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);

  &:hover:before {
    width: 100%;
  }
}

.header__logo:before {
  content: attr(data-text);
  -webkit-text-stroke-width: 1px;
  -webkit-text-stroke-color: black;
  color: $light-grey;
  position: absolute;
  top: 0;
  left: 0;
  width: 0;
  overflow: hidden;
  transition: 1s;
}

.header {
  display: flex;
  align-items: center;
}

.header__lang {
  display: flex;
  align-items: center;
}

.header__lang-list {
  display: flex;
  align-items: center;
  opacity: 0;
  visibility: hidden;
  transition: opacity .2s linear, visibility .2s linear;

  li {
    margin-left: 16px;
  }

  a {
    color: rgba(255, 255, 255, 0.6);
    font-size: 10px;
    text-transform: uppercase;
  }

  a.active {
    color: $white;
  }
}

.header__lang-trigger {
  cursor: pointer;
  max-width: 100%;
  width: 38px;
  height: 38px;
  border: 1px solid rgba(255, 255, 255, 0.1);
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
}

.header__lang:hover .header__lang-list {
  opacity: 1;
  visibility: visible;
}

@media (max-width: 1439px) {
  .header__logo {
    font-size: 24px;
  }

  .header {
    padding: 18px 17px 0;
  }
}

@media (max-width: 767px) {
  .header {
    justify-content: space-between;
    padding: 16px 16px 0;
  }

  .header__lang {
    flex-direction: column;
    align-items: flex-start;
    position: relative;
  }

  .header__lang-list {
    position: absolute;
    top: 100%;

    li:first-child {
      margin-left: 0;
    }
  }

  .header__logo {
    font-size: 20px;
  }
}

@media (max-width: 413px) {
  .header__logo {
    font-size: 18px;
  }

  .title {
    font-size: 34px;
  }
}