*,
*:before,
*:after {
  box-sizing: border-box;
}

body {
  margin: 0;
  background-color: $light-grey;

  font-size: 16px;
  font-weight: 400;
  line-height: 1.3;
  color: $dark-grey;
  font-family: 'GrtskGiga', sans-serif;
}

.container {
  width: 100%;
  max-width: 1230px;
  padding: 0 15px;
  margin: 0 auto;
}

.img {
  display: block;
  max-width: 100%;
  height: auto;
}

h1, h2, h3, h4, h5, h6 {
  margin: 0;
}

p {
  margin: 0;
}

a {
  text-decoration: none;
  color: inherit;
}

ul, ol {
  margin: 0;
  padding: 0;
  list-style: none;
}

.btn {
  font-family: inherit;
  font-size: inherit;
  padding: 0;
  border: none;
  background: none;
  text-decoration: none;
  cursor: pointer;

  &:focus {
    outline: none;
  }
}

.input {
  display: block;
  max-width: 100%;
  font-family: inherit;
  font-size: inherit;
  color: inherit;

  background: none;
  border: none;
  border-radius: 0;
  outline: 0;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}

.hamburger {
  display: none;
  width: 22px;

  span {
    display: block;
    height: 1px;
    background-color: $black;
    width: 100%;
    margin: 8px 0;
  }
}

.circle-wrapper {
  position: relative;
}

.text-round {
  position: absolute;
  bottom: 23px;
  right: 23px;
  z-index: 1;

  &:after {
    content: '';
    display: block;
    width: 8px;
    height: 8px;
    background-color: $black;
    border-radius: 50%;

    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
}

.circle {
  width: 578px;
  height: 578px;
  max-width: 100%;
  background-color: $yellow;
  border-radius: 50%;
  filter: blur(76px);
}

.title {
  width: 100%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-transform: uppercase;
  font-size: 82px;
  max-width: 1023px;
  text-align: center;
}

.wrapper {
  position: relative;
  width: 100%;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background: url("../img/diagonal-text.svg") no-repeat center;
  background-size: cover;
}

.menu {
  li {
    position: absolute;
  }

  li:first-child,
  li:nth-child(2) {
    top: 50%;
    transform: translateY(-50%);
  }

  li:first-child {
    left: 0;
    transform: rotate(-90deg);
  }

  li:nth-child(2) {
    right: 0;
    transform: rotate(90deg);
  }

  li:last-child {
    left: 50%;
    transform: translateX(-50%);
    bottom: 19px;
  }

  li:after {
    content: '';
    display: block;
    width: 0;
    height: 1px;
    background-color: $dark-grey;

    position: absolute;
    bottom: 0;
    left: 0;
    transition: width .2s linear;
  }

  li:hover:after {
    width: 100%;
  }
}

@media (max-width: 1439px) {
  .circle {
    width: 482px;
    height: 482px;
  }

  .title {
    font-size: 58px;
    max-width: 724px;
  }

  .menu a {
    font-size: 14px;
  }

  .menu {
    li:first-child {
      left: -20px;
    }

    li:nth-child(2) {
      right: -20px;
    }

    li:last-child {
      bottom: 12px;
    }
  }
}

@media (max-width: 1023px) {
  .title {
    max-width: 568px;
  }
}

@media (max-width: 767px) {
  .menu {
    display: none;
  }

  .hamburger {
    display: block;
  }

  .title {
    font-size: 36px;
    max-width: 353px;
  }

  .circle {
    width: 294px;
    height: 294px;
    filter: blur(46px);
  }

  .text-round {
    right: 23px;
    bottom: -26px;
  }

  .text-round img {
    width: 90px;
  }

  .text-round:after {
    width: 6px;
    height: 6px;
  }
}